@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap");
* {
  font-family: "Poppins", sans-serif;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul, ol {
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6, p {
  line-height: 0.8;
}

/* ====== nav_bar start here ====== */
#nav {
  background: transparent;
  color: #fff;
  position: absolute !important;
  left: 0;
  top: 0;
  width: 100%;
  padding: 16px 0;
}

.nav-item a {
  color: #BBBBCB;
}

#nav .nav-item .nav-link {
  padding: 0;
  font-size: 16px;
  font-weight: 500;
  line-height: 28px;
  transition: all linear 0.3s;
}

#nav .nav-item .nav-link:hover {
  color: #fff;
}

#nav .navbar-nav {
  column-gap: 32px;
  margin-right: 48px;
}

#nav .nav_btn {
  border-radius: 41px;
  padding: 16px 48px;
  border: 2px solid #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: 28px;
  color: #BBBBCB;
  transition: all linear 0.3s;
}
#nav .nav_btn:hover {
  background-color: #FCD980;
  color: #fff;
}

/* ======= bannner part start here====== */
#banner {
  background-color: #1C1E53;
  padding: 218px 0 218px 0;
}

#banner .banner_head {
  width: 582px;
  font-weight: 600;
  font-size: 54px;
  line-height: 74px;
  color: #fff;
}

.banner_pera {
  width: 582px;
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
  color: #fff;
  margin-top: 24px;
}

.banner_btn {
  margin-top: 64px;
  display: flex;
  column-gap: 40px;
  align-items: center;
}

.banner_btn .banner_button {
  border-radius: 41px;
  border: 2px solid #BBBBCB;
  padding: 16px 52px;
  font-size: 16px;
  font-weight: 500;
  line-height: 28px;
  color: #BBBBCB;
  transition: all linear 0.3s;
}

.banner_btn .banner_button:hover {
  background-color: #FCD980;
  color: #fff;
}

.banner_btn .banner_icon {
  color: #BBBBCB;
  transition: all linear 0.3s;
}

.banner_btn .banner_icon:hover {
  color: #fff;
}

.banner_btn i {
  margin-left: 16px;
}

.img_box {
  margin-top: 40px;
}

/* ===== work part start here===== */
#work {
  padding: 128px 0;
  background-color: #F4F6FC;
}

.work_head {
  font-weight: 600;
  font-size: 48px;
  line-height: 64px;
  color: #282938;
}

.work_pera {
  width: 405px;
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
  color: #282938;
  opacity: 0.7;
}

.work_heading .work_icon {
  color: #2405F2;
}

.work_heading i {
  margin-left: 16px;
}

.after_icon {
  width: 50px;
  height: 50px;
  background-color: #2405F2;
  border-radius: 8px;
  position: relative;
  overflow: hidden;
}

.logo_icon::after {
  width: 50px;
  height: 50px;
  background: #FCD980;
  content: "";
  position: absolute;
  top: 25px;
  left: -25px;
  border-bottom-left-radius: 8px;
}

.logo_icon_3 span {
  color: #fff;
  z-index: 444;
  position: absolute;
  left: 15px;
  top: 15px;
}

.logo_icon_3::before {
  width: 50px;
  height: 50px;
  background-color: #5239FA;
  border-bottom-left-radius: 8px;
  position: absolute;
  left: 10px;
  top: -10px;
  content: "";
  z-index: 333;
}

.work_header {
  font-weight: 500;
  font-size: 32px;
  line-height: 48px;
  color: #282938;
  margin-top: 16px;
}

.work_perageraph {
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
  color: #282938;
  opacity: 0.7;
}

/* ====== project part start here ===== */
#project {
  padding: 128px 0;
}

.project_img_theree {
  padding-top: 12px;
}

.project_header {
  font-weight: 600;
  font-size: 48px;
  line-height: 64px;
  color: #282938;
  margin-bottom: 64px;
}

.project_icon {
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
  color: #282938;
}

.project_icon_box i {
  margin-left: 16px;
}

.project_img_one {
  position: relative;
  cursor: pointer;
}

.project_overlay {
  position: absolute;
  left: -345px;
  top: 0;
  width: 47%;
  height: 100%;
  background-image: linear-gradient(rgba(50, 51, 99, 0.56), rgba(50, 51, 99, 0.83));
  opacity: 0;
  transition: all linear 0.8s;
}

.project_img_one:hover .project_overlay {
  opacity: 1;
  left: 0;
  top: 0;
}

.project_overlay h3 {
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  color: #FFFFFF;
}

.project_overlay p {
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
  color: #FFFFFF;
  opacity: 0.8;
}

.project_overlay a {
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
  color: #FCD980;
  transition: all linear 0.3s;
  margin-top: 40px;
}

.project_overlay a:hover {
  color: #fff;
}

.project_overlay i {
  margin-left: 16px;
}

.overlay_content {
  width: 85%;
  position: absolute;
  left: 30px;
  bottom: 50px;
}

.project_img_two {
  position: relative;
  cursor: pointer;
}

.two {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(50, 51, 99, 0.56), rgba(50, 51, 99, 0.83));
  opacity: 0;
  transition: all linear 0.8s;
}

.project_img_two:hover .two {
  opacity: 1;
}

.sublet h3 {
  padding: 10px;
}

.project_img_theree {
  position: relative;
  cursor: pointer;
}

.theree {
  position: absolute;
  left: 50px;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(50, 51, 99, 0.56), rgba(50, 51, 99, 0.83));
  opacity: 0;
  transition: all linear 0.4s;
}

.project_img_theree:hover .theree {
  opacity: 1;
  left: 0;
  top: 0;
}

.theree h3 {
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  color: #FFFFFF;
}

.theree p {
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
  color: #FFFFFF;
  opacity: 0.8;
}

.theree a {
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
  color: #FCD980;
  transition: all linear 0.3s;
  margin-top: 40px;
}

.theree a:hover {
  color: #fff;
}

.theree i {
  margin-left: 16px;
}

.sublet3 {
  width: 85%;
  position: absolute;
  left: 30px;
  bottom: 50px;
}

/* ====== frequendly part start here ==== */
#frequendly {
  padding: 130px 0;
}

.one_acc {
  display: flex;
  align-items: center;
  column-gap: 20px;
}

.one_acc .one_pera {
  color: #2405F2;
}

.fre_heading h4 {
  font-weight: 600;
  font-size: 38px;
  line-height: 56px;
  color: #282938;
  padding: 0 20px;
}

.fre_heading a {
  font-weight: 500;
  font-size: 18px;
  line-height: 32px;
  text-align: center;
  color: #2405F2;
  opacity: 0.87;
  margin-left: 20px;
}

.fre_pera .span_one {
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
  text-align: center;
  color: #2405F2;
  margin-right: 48px;
}

.fre_pera .span_one_one {
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
  color: #282938;
}

/* ====== features part start here ===== */
#features {
  background-color: rgb(244, 246, 252);
  padding: 128px 0;
}

.fet_content {
  text-align: center;
}

.fet_content p {
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
  text-align: center;
  color: #282938;
}

.fet_content h2 {
  font-weight: 600;
  font-size: 48px;
  line-height: 64px;
  text-align: center;
  color: #282938;
  padding: 0 300px;
  margin-bottom: 47px;
}

.fet_full_content {
  padding: 53px 14px 48px 25px;
  margin-top: 24px;
  margin-right: 5px;
  border-radius: 10px;
  transition: all linear 0.3s;
  cursor: pointer;
  background-color: #fff;
}

.fet_full_content:hover {
  -webkit-box-shadow: 0px -1px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px -1px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px -1px 5px 0px rgba(0, 0, 0, 0.75);
  margin: 20px 0;
}

.fet_full_content h3 {
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
  color: #282938;
  margin: 22px 0 12px 0;
}

.fet_full_content p {
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
  color: #282938;
  opacity: 0.7;
}

.fet_icon i {
  color: rgb(36, 5, 242);
  font-size: 32px;
  margin-bottom: 16px;
  transition: all linear 0.3s;
}

.fet_icon i:hover {
  color: #333;
  cursor: pointer;
}

/* ====== building part start her ===== */
.building_img_box {
  position: relative;
}

.building_overlay {
  position: absolute;
  left: -300px;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(28, 30, 83, 0.295), rgba(28, 30, 83, 0.459));
  padding: 96px 96px 216px 96px;
  opacity: 0;
  transition: all linear 0.4s;
}

.building_img_box:hover .building_overlay {
  opacity: 1;
  left: 0;
  top: 0;
  cursor: pointer;
}

.building_overlay h2 {
  font-weight: 600;
  font-size: 54px;
  line-height: 74px;
  color: #FFFFFF;
  width: 464px;
}

.building_overlay p {
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
  width: 464px;
  color: #FFFFFF;
}

.from_content {
  background-color: #1C1E53;
  width: 100%;
  height: 100%;
  padding: 55px 55px 25px 96px;
}

.from_content h3 {
  font-weight: 500;
  font-size: 32px;
  line-height: 48px;
  color: #FFFFFF;
}

.from_content p {
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
  color: #F4F6FC;
  margin-bottom: 40px;
}

.input {
  width: 431px;
  height: 63px;
  padding-left: 15px;
  margin-bottom: 15px;
  border: 2px solid #BBBBCB;
  border-radius: 10px;
  text-transform: capitalize;
  background-color: transparent;
}

.input_button {
  width: 431px;
  height: 63px;
  text-align: center;
  border: 2px solid #BBBBCB;
  border-radius: 50px;
  text-transform: capitalize;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all linear 0.3s;
  margin-top: 20px;
  margin-bottom: 28px;
}

.input_button:hover {
  background-color: #FCD980;
  color: #000000;
}

.input_btn {
  color: #ffffff;
}

.input_btn_last {
  display: flex;
  justify-content: center;
  align-items: center;
}

.input_btn_last a {
  color: #fff;
}

.input_btn_last i {
  margin-left: 16px;
}

/* ====== client part start here ===== */
#client {
  background-color: rgba(244, 246, 252, 0.5);
  padding: 130px 0;
  margin-top: 20px;
}

.client_heading h3 {
  font-weight: 600;
  font-size: 38px;
  line-height: 56px;
  color: #282938;
  margin-bottom: 16px;
}

.client_heading p {
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
  color: #282938;
  opacity: 0.7;
}

.client_content h3 {
  font-weight: 500;
  font-size: 30px;
  line-height: 48px;
  color: #282938;
  margin-bottom: 48px;
}

.client_img_box {
  display: flex;
  column-gap: 20px;
}

.client_img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
}

.img_content p {
  font-weight: 500;
  font-size: 18px;
  color: #282938;
}

.img_content span {
  font-weight: 500;
  font-size: 12px;
  color: #282938;
}

/* ====== foooter part strat here ====== */
#footer {
  background-color: #1C1E53;
  padding-top: 95px;
}

.footer_img p {
