}

.footer_icon_main {
  display: flex;
  align-items: center;
  column-gap: 20px;
}

#footer_last {
  padding: 32px 0;
}

.nav_footer {
  display: flex;
  column-gap: 22px;
}

.nav_item .nav_link {
